.review-slider{
    background-color: #F8F8F8;
    &__wrapper{
        padding: 42px 0;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 25px;
        @media screen and (max-width:1024px){
            padding: 32px 0;            
        }
        @media screen and (max-width:800px){
            padding: 30px 0;            
        }
    }
    &__icon{
        margin-right: auto;
        @media screen and (max-width:800px){
            display: none;            
        }
    }
    &__main{
        display: flex;         
        .review-main__icon{ 
            display: flex;
            flex-grow: 1;
            justify-content:  center;
            align-items: center;
            min-width: 170px;
            min-height: 170px;
            margin-left: 30px;
            border: 1px solid #95E1D3;
            @media screen and (max-width:1024px){
                min-width: 115px;
                min-height: 115px;
                margin-right: 10px;
            }
            @media screen and (max-width:800px){
                display: none;            
            }
        }
        .review-main__text{            
            margin-right:60px;
            margin-left: 30px;
            .reviews__slaider-btns{
                display: none;
                flex-direction: row;
                .slaider-btn{
                    display: none;
                  }
                .slaider-btn + label::before{  
                    content: "";
                    display: block;  
                    width: 10px;
                    height: 10px;
                    border: 1px solid #86878B;
                    border-radius: 50%;
                    margin: 10px;
                  }
                  .slaider-btn:checked + label::before{
                    background-color: #86878B;
                  }
                @media screen and (max-width:800px){
                    display: flex;
                    justify-content: center; 
                    margin-top: 25px;        
                }
            }
            @media screen and (max-width:1024px){
                margin-right:38px;
                margin-left: 20px;
            }
            @media screen and (max-width:1024px){
                margin-right: 0px;
                margin-left: 0px;
            }
            .review-text__discription{
                p{
                    max-width: 770px;
                    color: #A9AFB1;
                    font-style: italic;
                    font-weight: 500;
                    font-size: 24px;
                    line-height: 28px;
                    @media screen and (max-width:1024px){
                        font-size: 18px;
                        line-height: 21px;
                    }
                    @media screen and (max-width:800px){                      
                        font-size: 14px;
                        line-height: 16px;
                    }
                }
            }
            .review-text__name{
                display: flex;
                align-items: center;
                margin-top: 21px;
                p{
                    margin-left: 56px;
                    font-family: @KaushanScript;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 30px;
                    line-height: 44px;
                    @media screen and (max-width:1024px){
                        font-size: 24px;
                        line-height: 35px;
                    }                    
                    @media screen and (max-width:800px){                                          
                        font-size: 18px;
                        line-height: 26px;
                    }
                }
            }
        }
    }
    @media screen and(max-width:1024px){
        svg{
            scale: 0.7;
        }        
    }
}