.our-work{
    &__wrapper{
        padding-top: 84px;
        @media screen and(max-width:1024px){
            padding-top: 60px;       
        }
        .title-centr{
            h2{
                margin-bottom: 24px;
                @media screen and(max-width:1024px){
                    margin-bottom: 20px;
                }
            }
            .mogo-line{
                margin-bottom: 44px;
                @media screen and(max-width:1024px){
                    margin-bottom: 40px;
                }
            }
        }
        .discription-centr{
            margin-bottom: 86px;
            @media screen and(max-width:1024px){
                margin-bottom: 66px;                
            }
        }
    }
    &__cards{
        display: grid;
        grid-template: repeat( 2, minmax(200px,1fr))/ repeat(4, minmax(200px,1fr));
        div:nth-child(3){            
            grid-row: 1/3;
            grid-column: 3/4;
        }
        .our-work__card{
            position: relative;
            img{
                height: 100%;
                width:100%;
            }
            .our-work__shadow{
                position: absolute;
                background: linear-gradient(360deg, rgba(237, 215, 137, 0.9) 0%, rgba(233, 130, 131, 0.9) 100%);
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: 0.5s ease;
                &:hover{
                    opacity: 1;
                    transition: 0.5s;
                }                 
                .shadow-img{
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    width: 100%;
                    height: 100%;
                    @media screen and(max-width:1024px){
                        scale: 0.7;
                    }
                    svg path{
                        fill: #FFFFFF;
                        width: 40px;
                        height: 40px;
                    }
                    h5{
                        padding-top: 40px;
                        padding-bottom: 24px;
                        margin-bottom: 0;
                        font-style: normal;
                        font-weight: 700;
                        font-size: 24px;
                        line-height: 28px;
                        color: #FFFFFF;
                    }
                    p{

                        font-style: normal;
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 21px;
                        color: #FFFFFF;
                    }  
                }
                            
            }
        }
    }
}